import React, { Component } from 'react';
import 'react-calendar/dist/Calendar.css';
import "react-datepicker/dist/react-datepicker.css";
import { Grid, Row, Col, Button } from "react-bootstrap";
var moment = require('moment');

class FilterDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }
    crossClick(e) {
        e.preventDefault();
        this.props.onHide()
    }

    render() {
        let data = this.props.data;
        let from_symbol = JSON.parse(data.from_symbols);
        let to_symbol = JSON.parse(data.to_symbols);
        from_symbol = from_symbol.join(', ');
        to_symbol = to_symbol.join(', ');
        return (
            <div className="content">
                <Grid fluid>
                    <div className="symbol-box">
                        <div className="cross_div"><a onClick={(e) => this.crossClick(e)}><i className="fa fa-times" aria-hidden="true"></i></a></div>
                        <div className="detail-text"><h3> {data.profile_name} </h3></div>
                        <Row>
                            <Col md={6}>
                                <div className="card">
                                    <div className="detail_account" style={{ padding: '10px 20px 0px' }}>
                                        <span style={{ fontWeight: 'bold' }}>Account A</span>
                                    </div>
                                    <table className="table mt20">
                                        <tbody>
                                            <tr>
                                                <td className="b-clr">
                                                    <span>Account :</span>
                                                </td>

                                                <td className="b-clr text-center" style={{ width: '40%' }}>
                                                    {data.accountFromInfo[0].login}
                                                </td>
                                            </tr>
                                            <tr>
                                                <td className="b-clr ">
                                                    <span>From :  </span>
                                                </td>
                                                <td className="b-clr text-center datepicker_filter">
                                                    {moment(data.startdateFrom).format("DD/MM/YYYY")}

                                                </td>
                                            </tr>
                                            <tr>
                                                <td className="b-clr ">
                                                    <span>To :  </span>
                                                </td>
                                                <td className="b-clr text-center datepicker_filter">
                                                    {data.enddateFrom  !== null ? moment(data.enddateFrom).format("DD/MM/YYYY") :"-"}

                                                </td>
                                            </tr>
                                            <tr>
                                                <td className="b-clr ">
                                                    <span>Symbol :  </span>
                                                </td>
                                                <td className="b-clr text-center" style={{ width: '40%' }}>
                                                    {from_symbol}
                                                </td>
                                            </tr>

                                        </tbody>
                                    </table>
                                </div>
                            </Col>
                            <Col md={6}>
                                <div className="card">
                                    <div className="detail_account" style={{ padding: '10px 20px 0px' }}>
                                        <span style={{ fontWeight: 'bold' }}>Account B</span>
                                    </div>

                                    <table className="table mt20">
                                        <tbody>
                                            <tr>
                                                <td className="b-clr ">
                                                    <span>Account :</span>
                                                </td>
                                                <td className="b-clr text-center" style={{ width: '40%' }}>
                                                    {data.accountToInfo[0].login}
                                                </td>
                                            </tr>
                                            <tr>
                                                <td className="b-clr ">
                                                    <span>From :  </span>
                                                </td>
                                                <td className="b-clr text-center datepicker_filter">
                                                    {moment(data.startdateTo).format("DD/MM/YYYY")}

                                                </td>
                                            </tr>
                                            <tr>
                                                <td className="b-clr ">
                                                    <span>To :  </span>
                                                </td>
                                                <td className="b-clr text-center datepicker_filter">
                                                    {data.enddateTo!=null ? (moment(data.enddateTo).format("DD/MM/YYYY")) : '-'}

                                                </td>
                                            </tr>
                                            <tr>
                                                <td className="b-clr">
                                                    <span>Symbol :  </span>
                                                </td>
                                                <td className="b-clr text-center" style={{ width: '40%' }}>
                                                    {to_symbol}
                                                </td>
                                            </tr>

                                        </tbody>
                                    </table>
                                </div>
                            </Col>
                            {data.commission_acount_id !== 0 && <Col md={12}>
                                <div className="card bottom">
                                    <div className="detail_account" style={{ padding: '10px 20px 0px' }}>
                                        <h3 style={{ fontWeight: 'bold' }}>Commission</h3>
                                    </div>
                                    <table className="table mt20">
                                        <tbody>
                                            <tr>
                                                <td className="b-clr text-center" style={{ width: '40%' }}>
                                                    <span>Account :</span>
                                                </td>
                                                <td className="b-clr text-center" style={{ width: '40%' }}>
                                                {data.accountCommissionInfo[0].login}

                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>

                            </Col>}
                        </Row>

                    </div>
                </Grid>
            </div>
        );
    }
}

export default FilterDetail;
